// 组织树数据
export const getTreeData = (params) =>
  defHttp.post(
    {
      url: Api.treeData,
      params,
    },
    {},
  );

// 列表数据
export const getDroneInspectionList = (params) =>
  defHttp.post(
    {
      url: Api.areaList,
      params,
    },
    {},
  );

// 获取无人机巡检
export const getIndex = (params) =>
  defHttp.get(
    {
      url: Api.getIndexUrl,
      params,
    },
    {},
  );

// 新增无人机巡检
export const insertIndex = (params) =>
  defHttp.post(
    {
      url: Api.createIndexUrl,
      params,
    },
    {},
  );
//执行巡检
export const performData = (params) =>
  defHttp.post(
    {
      url: Api.performUrl,
      params,
    },
    {},
  );
export const saveAndPerform = (params) =>
  defHttp.post(
    {
      url: Api.saveAndPerformUrl,
      params,
    },
    {},
  );
// 修改无人机巡检
export const updateIndex = (params) =>
  defHttp.put(
    {
      url: Api.updateIndexUrl,
      params,
    },
    {},
  );

export const saveData = (params) => {
  if (!params.id) {
    return insertIndex(params);
  }
  return updateIndex(params);
};

// 删除无人机巡检
export const deleteIndex = (params) =>
  defHttp.delete(
    {
      url: Api.deleteIndexUrl,
      params,
    },
    {},
  );

// 导出无人机巡检
export const exportExcel = (params) =>
  defHttp.download(
    {
      url: Api.exportExcelIndexUrl,
      params,
    },
    {},
  );
